/**
 * The visual state of a square.
 */
enum SquareState {
  Empty,
  BlackLegalMove,
  WhiteLegalMove,
  BlackDisc,
  WhiteDisc,
}

export default SquareState;

/**
 * Returns whether the given state is a legal move for Black or White.
 */
export function isLegalMoveState(state: SquareState) {
  return state === SquareState.BlackLegalMove || state === SquareState.WhiteLegalMove;
}

/**
 * Returns whether the given state is a black or white disc.
 */
export function isDiscState(state: SquareState) {
  return state === SquareState.BlackDisc || state === SquareState.WhiteDisc;
}
